@import '../../style/global.scss';

.played-container {
  position: absolute;
  overflow: hidden;
  top: 0;
  min-height: 100vh;
  min-width: 100vw;
  opacity: 1;
  background-color: transparentize(darken($mainColor, 40%), 0.1);

  h2 {
    margin-top: 40px;
  }
}

.connecting {
  position: absolute;
  overflow: hidden;
  top: -100px;
  min-width: 100vw;
  min-height: 130vh;
  background: linear-gradient(45deg, white 25%,
    #eee 25%, #eee 50%,
    white 50%, white 75%,
    #eee 75%);
  background-size: 100px 100px;
  animation: connecting 3s linear infinite;
}

.connecting-container {
    position: absolute;
    overflow: hidden;
    top: 0;
    min-height: 100vh;
    min-width: 100vw;
    opacity: 0;
}

.connecting {
    position: absolute;
    overflow: hidden;
    top: -100px;
    min-width: 100vw;
    min-height: 130vh;
    background: linear-gradient(45deg, white 25%,
      #eee 25%, #eee 50%,
      white 50%, white 75%,
      #eee 75%);
    background-size: 100px 100px;
    animation: connecting 3s linear infinite;
}

@keyframes connecting {
  0% {
      top: -100px;
  }
  100% {
      top: 0px;
  }
}

.connecting h5 {
    text-align: center;
    color: #666;
    margin-top: 70vh;
}

/* Connecting */
.lds-ripple {
    display: inline-block;
    position: absolute;
    left: calc(50vw - 32px);
    top: calc(50vh - 32px);
    width: 64px;
    height: 64px;
}
.lds-ripple div {
    position: absolute;
    border: 4px solid #000;
    opacity: 1;
    border-radius: 50%;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
    animation-delay: -0.5s;
}
@keyframes lds-ripple {
    0% {
        top: 28px;
        left: 28px;
        width: 0;
        height: 0;
        opacity: 1;
    }
    100% {
        top: -1px;
        left: -1px;
        width: 58px;
        height: 58px;
        opacity: 0;
    }
}

/* Loading */
.lds-default {
    display: inline-block;
    position: absolute;
    left: calc(50vw - 32px);
    top: calc(50vh - 32px);
    width: 64px;
    height: 64px;
  }
  .lds-default div {
    position: absolute;
    width: 5px;
    height: 5px;
    background: #333;
    border-radius: 50%;
    animation: lds-default 1.2s linear infinite;
  }
  .lds-default div:nth-child(1) {
    animation-delay: 0s;
    top: 29px;
    left: 53px;
  }
  .lds-default div:nth-child(2) {
    animation-delay: -0.1s;
    top: 18px;
    left: 50px;
  }
  .lds-default div:nth-child(3) {
    animation-delay: -0.2s;
    top: 9px;
    left: 41px;
  }
  .lds-default div:nth-child(4) {
    animation-delay: -0.3s;
    top: 6px;
    left: 29px;
  }
  .lds-default div:nth-child(5) {
    animation-delay: -0.4s;
    top: 9px;
    left: 18px;
  }
  .lds-default div:nth-child(6) {
    animation-delay: -0.5s;
    top: 18px;
    left: 9px;
  }
  .lds-default div:nth-child(7) {
    animation-delay: -0.6s;
    top: 29px;
    left: 6px;
  }
  .lds-default div:nth-child(8) {
    animation-delay: -0.7s;
    top: 41px;
    left: 9px;
  }
  .lds-default div:nth-child(9) {
    animation-delay: -0.8s;
    top: 50px;
    left: 18px;
  }
  .lds-default div:nth-child(10) {
    animation-delay: -0.9s;
    top: 53px;
    left: 29px;
  }
  .lds-default div:nth-child(11) {
    animation-delay: -1s;
    top: 50px;
    left: 41px;
  }
  .lds-default div:nth-child(12) {
    animation-delay: -1.1s;
    top: 41px;
    left: 50px;
  }
  @keyframes lds-default {
    0%, 20%, 80%, 100% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.5);
    }
  }
  
